@import url('https://fonts.googleapis.com/css2?family=Lemon&family=Salsa&display=swap');
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f5f5f5;
}

.container {
    max-width: 600px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
    color: #1e1e1e;
    text-align: center;
    font-family: 'Lemon', serif;
}

form {
    margin-bottom: 20px;
    font-family: 'Salsa', cursive;
}

label {
    display: block;
    margin-bottom: 10px;
    color: #333;
    font-family: 'Salsa', cursive;
}

input[type='checkbox'] {
    margin-right: 5px;
}

input[type='text'] {
    width: 100%;
    padding: 8px;
    margin-top: 5px;
    margin-bottom: 15px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: 'Salsa', cursive;
}

button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: 'Salsa', cursive;
}

button:hover {
    background-color: #45a049;
}

h2,
ul {
    font-family: 'Salsa', cursive;
}

ul {
    list-style: none;
    padding: 0;
}

li {
    margin-bottom: 10px;
    color: #333;
}